.clearfix() {
  &:after {
    content: '';
    display: block;
    clear: both;
  }
}

.box-border(@color,@padding) {
  padding-bottom: @padding;
  border-bottom: 3px solid @color;
}

.hover-link(@color,@padding,@height) {

}

.plh-c(@color,@fz,@ff) {
  &::-webkit-input-placeholder {
    color: @color;
    font-size: @fz;
    font-family: @ff;
  }
  &::-moz-placeholder {
    color: @color;
    font-size: @fz;
    font-family: @ff;
  }
  /* Firefox 19+ */
  &:-moz-placeholder {
    color: @color;
    font-size: @fz;
    font-family: @ff;
  }
  /* Firefox 18- */
  &:-ms-input-placeholder {
    color: @color;
    font-size: @fz;
    font-family: @ff;
  }
}

.box-std-m(){
  @media @nodesktop-xl{
    float: none;
    width: 600px;
    margin: 0 auto 30px auto;
  }
}