
.header {

  &.scroll-header {
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    background: @white;
    z-index: 9999;
    box-shadow: 0 5px 21px rgba(0, 0, 0, 0.25);

    .search-lawyer__form--wrp__h {
      display: block;
    }

    .header-search-form {
      display: block;
    }
  }
}

.promo-bar {
  color: @white;
  font-weight: 700;

  @media @nodesktop{
    display: none;
  }

  a {
    color: @white;
    text-transform: uppercase;
    text-decoration: underline;

    &:hover {
      text-decoration: none;
    }
  }

  .clearfix;
  background: @main-red;
  padding: 11px @base-padding-d;

  .promo-bar__text {
    float: left;
    text-transform: uppercase;

  }

  .promo-bar__btn {
    float: right;
  }
}

.main-logo {
  max-width: 300px;
  display: block;
  img {
    width: 100%;

    @media @nodesktop{
      height: 66px;
      width: 125px;
    }
  }
}

.nav-top {
  padding-right: @base-padding-d;
}

.top-logo {
  @media @nodesktop{
    margin-left: 20px;
  }

  strong {
    display: block;
  }
}

.header-container {
  padding: 15px 0 0 @base-padding-d;

  @media @nodesktop{
    padding: 0;
    height: 66px;

  }
}

.super-nav {
  @media @nodesktop{
    position: absolute;
    top: 26px;
    right: 50px;
  }

  @media @desktop{
    padding-bottom: 14px;
    border-bottom: 1px solid #c2c2c3;
  }
}

.scroll-header{
  .super-nav{
    @media @desktop{
      padding-bottom: 0;
      border-bottom: none;
    }
  }
}

.super-nav__item--mobile {
  @media @desktop{
    display: none;
  }
}

.login-text {
  @media @nodesktop{
    display: none;
  }
}

.login-icon__nav {
  display: none;
  @media @nodesktop{
    display: block;
  }
}

.nominate {
  text-transform: uppercase;
  color: @main-red;
  font-weight: 700;
}
