// Width variables (appears count calculates by raw css)
// Appears 4 times

.collapse-button {
  box-sizing: border-box;
  color: @white;
  cursor: pointer;
  font-size: 14px;
  position: absolute;
  right: 20px;
  top: 30px;

  &:hover {
    color: @white;
  }
  &:focus {
    color: @white;
  }
  .icon-bar {
    background-color: @main-red;
    display: block;
    height: 2px;
    margin-bottom: 4px;
    width: 19px;

    &:last-child {
      margin-bottom: 0;
    }
  }
}

ul.main-nav__list {
  float: right;
  list-style-type: none;
  margin: 0;
  padding: 0;

  &:before {
    content: '';
    display: table;
  }

  &:after {
    clear: both;
    content: '';
    display: table;
  }

  li {
    background-color: none;
    padding: 6px 20px;
    position: relative;

    &.super-nav__item--mobile{
      padding: 0;
      margin-right: 20px;
    }

    ul {
      list-style-type: none;
      margin: 0;
      > li {
        &:hover {
          > a {
            color: #188dc2;
          }
        }
        a {
          font-size: 15px;
          font-weight: 700;
        }

        ul{
          top: 15px;
        }
      }
    }
    .main-nav__link {
      -moz-transition: color 0.5s ease-out;
      -o-transition: color 0.5s ease-out;
      -webkit-transition: color 0.5s ease-out;
      color: #636466;
      display: block;
      font-family: 'Lato', Arial, sans-serif;
      font-size: 17px;
      font-weight: 700;
      text-transform: uppercase;
      transition: color 0.5s ease-out;
    }
    &:hover {
      .main-nav__link {
        color: @blue;
      }
    }
    a {
      &:hover {
        text-decoration: none;
      }
    }
      .sub-toggle {
      cursor: pointer;
      height: 100%;
      position: absolute;
      right: 0;
        top: 2px;

      @media @nodesktop{
        top: 10px;
      }

      width: 48px;
      z-index: 999;
    }
    .sub-toggle > i {
      color: @blue;
      display: inline-block;
      font-size: 12px;
      vertical-align: middle;
    }
  }

  li > ul {
    background: @white;
    display: none;
    left: -25px;
    min-width: 215px;
    position: absolute;
    right: 0;
    top: 100%;
    width: 100%;
    z-index: 999;

    @media @desktop{
      padding: 0 @base-padding-d;
    }

    li {
      @media @desktop{
        margin-bottom: 20px;
      }

    }
  }

  li > ul > li {
    ul {
      display: none;
      left: 100%;
      position: absolute;
      top: 0;
      width: 100%;
      z-index: 999;
      border-bottom: 1px solid #c2c2c2;
      margin-bottom: 15px;
    }
  }
}

ul.main-nav__list.collapsed {
  li {
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    display: block;
    width: 100%;
    ul {
      li {
        a {

          box-sizing: border-box;
          display: block;
          padding: 10px 0;
        }
      }
    }

    .sub-toggle {
      text-align: right;
      height: 40px;
    }
  }
  li > ul {
    display: none;
    position: static;
  }
}

.ul.main-nav__list {
  li {
    ul {
      li {
        &:hover {
          a {
            color: @blue;
          }
        }
      }
    }
  }
}

ul.main-nav__list li.active-menu .main-nav__link {
  color: @blue;
}

@media @nodesktop {
  ul.main-nav__list li .sub-toggle {
    display: block;
  }
}

.main-nav__list {
  &.collapsed {
    position: absolute;
    left: 0;
    right: 0;
    z-index: 9;
    width: 100%;
    background: @white;
    top: 66px;
    padding: 0 @base-padding-m;
  }
}

.main-nav__item {
  &.has-submenu {
    ul {
      li {
        padding: 0;
      }
    }
  }
}
.mobile-collapser{
  display: none!important;

  @media @nodesktop{
    display: inline-block!important;
  }
}

.chevron-menu{
  display: none;
  @media @desktop{
    width: 12px;
    height: 18px;
    display: inline-block;
    background: url('../img/arrow-sprite.png') no-repeat -11px 0;
  }

}

ul.main-nav__list{
  @media @desktop{
    >li{
      >.sub-toggle{

        display: none;

      }

      .sub-toggle{
        text-align: right;

        @media @nodesktop{
          &:before{
            content: none;
          }
        }
      }
      ul{
        li{
          &:hover{
            .sub-toggle{
              .chevron-menu{
                background: url('../img/arrow-sprite.png') no-repeat 0 0;
              }
            }
          }
        }
      }
    }
  }




}
