.search-lawyer__form--wrp {
  background: @white;
  padding: 6px;

  @media @mobile{
    padding: 1px;
  }

  .search-box {
    display: flex;
    height: 33px;
    align-items: center;

    @media @mobile-x{
      height: 30px;
    }

    .search-lawyer__form--field {
      flex-grow: 1;

      @media @nomobile{
        border-left: 1px solid #c1c1c2;
      }

      line-height: 17px;
      height: 17px;
      padding-left: 10px;
      font-style: italic;
    }

    .search-lawyer__form--submit {
      background: @blue-ocean;
      color: @white;
      padding: 11px 15px;
      font-weight: 700;
      text-transform: uppercase;

      @media @mobile-x{
        padding: 7px 5px;
        font-size: 12px;
      }
    }
  }
}

.search-lawyer__form--wrp__h {
  border: 1px solid #c2c2c3;
  margin-bottom: 8px;
  display: none;

  @media @mobile-x{
    height: 34px;
    display: block;

  }
}

.search-form__container {

  @media @mobile-x{
    padding: 9px 9px 0 9px;
    border-top: 1px solid #c2c2c2;
  }
}

.search-lawyer__form--wrp__h {
  float: right;
  @media @desktop{
    margin-right: 26px;
  }

  @media @nodesktop{
    width: 100% !important;
  }
}

.search-lawyer__form--wrp__h{
  @media @desktop{
    .search-box{
      height: 25px;

      .search-lawyer__form--field{
        border-left: none;
      }

      .search-lawyer__form--submit-h{

          width: 18px;
          height: 18px;
          display: block;
          background: transparent url('../img/s-h.png') no-repeat;
          font-size: 0;

      }
    }
  }

  @media @nodesktop{

    .search-box{

      .search-lawyer__form--submit-h{

        background: @blue-ocean;
        color: @white;
        padding: 11px 15px;
        font-size: 15px;
        font-weight: 700;
        text-transform: uppercase;
        display: block;

      }
    }
  }

  @media @mobile-xs{
    .search-box{

      .search-lawyer__form--submit-h{

        background: @blue-ocean;
        color: @white;
        padding: 7px 5px;
        font-size: 12px;
        font-weight: 700;
        text-transform: uppercase;
        display: block;

      }
    }
  }

}

#inquiries-form{
  input[type='text'],
  input[type='email'],
  input[type='number'],
  input[type='tel']{
    border: 1px solid @form-gray;
    height: 45px;
    padding: 0 18px;
    font-style: italic;
    font-size: 14px;
    color: @main-gray;
    display: block;
    width: 100%;
    font-weight: 400;
    background: @white;
    .plh-c(@main-gray,14px,@base-font);

    &:focus{
      border: 1px solid darken(@form-gray, 50%);
    }
  }

  .form-item__h{
    width: calc(~'50% - 20px');
    float: left;
    margin: 0 20px 20px 0;
    
    @media @mobile-x{
      width: 100%;
      float: none;
      margin-right: 0;
    }
  }

  .form-item__wrp{
    .clearfix;
  }

  .form-item__f{

    @media @nomobile-x{
      padding-right: 20px;
    }

    margin-bottom: 20px;

    .clearfix;

    textarea{
      width: 100%;
      resize: none;
      display: block;
      min-height: 100px;
      padding: 15px;
      color: @main-gray;
      font-style: italic;
      outline: none;
      border: 1px solid @form-gray;

      &:focus{
        border: 1px solid darken(@form-gray, 50%);
      }
    }
  }

  .smbt-btn__inq{
    float: right;
    text-transform: uppercase;
    font-weight: 700;
    font-size: 14px;
    background: @blue-ocean;
    text-align: center;
    padding: 10px 44px;
    line-height: 1;
    color: @white;
    transition: background .3s;

    &:hover{
      background: darken(@blue-ocean,20%);
    }

    @media @mobile-xx{
      float: none;
      width: 100%;
      display: block;
    }
  }

  select{
    display: block;
    width: 100%;
  }

  .chosen-container{
    width: 100%!important;
    font-size: 14px;
    font-family: @base-font;
  }

  .chosen-container-single{
    &.chosen-container-active{
      .chosen-single{
        border: 1px solid darken(@form-gray, 50%);
      }
    }

    .chosen-single{
      border-radius: 0;
      border: 1px solid @form-gray;
      box-shadow: none;
      background: @white;
      height: 45px;
      padding: 0 18px;
      font-style: italic;
      font-size: 14px;
      display: block;
      color: @main-gray;
      font-family: @base-font;

      span{
        line-height: 45px;
        display: block;
        color: @main-gray;
        font-family: @base-font;
      }

      div{
        b{
          &:after{
            content: "";
            width: 9px;
            height: 9px;
            margin: -10px 0 0;
            border-right: 2px solid @main-gray;
            border-bottom: 2px solid @main-gray;
            position: absolute;
            top: 50%;
            right: 16px;
            transform: rotate(45deg);
          }
        }
      }
    }
    &.chosen-with-drop{
      .chosen-single{
        div{
          b{
            &:after{
              border-right: none;
              border-bottom: none;
              border-left:2px solid @main-gray ;
              border-top: 2px solid @main-gray;
            }
          }
        }
      }

    }
  }
}