
.super-nav__link {
  transition: all .4s;
  overflow: hidden;
  transform: translateZ(0);
  position: relative;

  @media @desktop{
    &:before {
      content: '';
      position: absolute;
      right: -20px;
      top: 3px;
      height: 17px;
      width: 1px;
      background: @blue;
    }
  }

}

.super-nav__list {
  text-align: right;

  .super-nav__item {
    display: inline-block;
    padding: 0 20px;

    &:hover {
      a {
        .icon-cart {
          fill: @main-red;
        }
      }

      .super-nav__link {
        position: relative;
        color: @main-red;

        &:after {

          left: 0;
          right: 0;
          width: 100%;

        }
      }
    }

    &.hide-scroll {
      display: none;
    }

    @media @nodesktop{
      padding: 0;
      margin-right: 20px;
    }

    &.hide {
      @media @nodesktop{
        display: none;
      }
    }

    li:not(:last-child) {
      &:after {
        content: '';
        width: 1px;
        height: 20px;
        display: inline-block;
        background: @blue;
        position: absolute;
        right: 0;
        top: -2px;

        @media @nodesktop{
          content: none;
        }
      }
    }

    &:last-child {
      padding-right: 0;

      &:after {
        content: none;
      }
    }
  }
}

.main-nav{
  .clearfix;
}

.super-nav__link--nominate {
  font-weight: 700;
  text-transform: uppercase;
  color: @main-red;
}

body.open-menu {
  .main-nav {
    @media @nodesktop{
      position: fixed;
      left: 0;
      right: 0;
      z-index: 8;
      width: 100%;
      height: 100%;
      overflow: auto;
      top: 0;
      bottom: 0;
    }
  }
}

.main-nav__list {

  .main-nav__item {
    display: inline-block;
    margin-right: 30px;
    padding: 17px 0;


    &.last-main {
      margin-right: 0;

      @media @nodesktop{
        margin-bottom: 20px;
      }
    }

    @media @nodesktop{
      border-bottom: 1px solid #c2c2c2;
    }
  }
}

.scroll-header{
  .main-nav__list{
    .main-nav__item{
      @media @desktop{
        padding-top: 9px;
      }
    }
  }

  ul.main-nav__list{
    li{
      .main-nav__link{
        font-size: 15px;
      }
    }
  }
}

.heading-nav__list {
  background: @main-blue;
  text-align: center;
  padding: @base-padding-d 0;
  margin-bottom: 45px;

  @media @mobile-x{
    padding: @base-padding-m * 2 @base-padding-m;
  }
}

.heading-nav__item {
  display: inline-block;
  margin-right: 9px;

  @media @mobile{
    width: 45%;
    margin: 10px;
  }

  @media @mobile-x{
    width: 100%;
    margin: 0 0 20px 0;

    &:last-child {
      margin: 0;
    }
  }

  &:last-child {
    @media @nomobile{
      margin-right: 0;
    }

  }

  .heading-nav__link {
    display: block;
    padding: 10px 20px;
    background: @white;
    color: @blue;
    text-transform: uppercase;
    transition: all .4s;
    font-weight: 700;

    &.heading-nav__law{
      background: @main-red;
      color: @white;
    }
  }

  &.active-menu {
    .heading-nav__link {
      color: @blue;
    }
  }

  &:hover {
    .heading-nav__link {
      color: @white;
      background: @main-red;
    }
  }
}

.main-nav__list {
  &.collapsed {
    .super-nav__item {
      margin-bottom: 20px !important;
    }
  }
}

.super-nav__item--mobile {
  @media @nodesktop{
    &:after {
      content: none;
    }
  }
}

