.introduction{
  margin: 0 auto;
  max-width: 720px;

  .l25__descr-text{
    text-align: center;
  }
}


.h-list__item{
  width: 100%;
  margin-bottom: 43px;
  display: flex;
 min-height: 85px;
  align-items: center;

  &:last-child{
    margin-bottom: 0;
  }
}

.founder{
  float: left;
  width: calc(~'50% - 1px');
  &:first-child{
    margin-right: 2px;
  }
}

.leader-item{
  float: left;
  width: 50%;
  height: 260px;
}

.leader-item__link{
  display: block;
  position: relative;
  z-index: 4;
  height: 100%;
  overflow: hidden;

  @media @nomobile{
    &:before{
      width: 100%;
      height: 100%;
      left: 0;
      top: 0;
      right: 0;
      position: absolute;
      content: '';
      bottom: 0;
      z-index: 2;
      background: rgba(54, 105, 179, 0.9);
      display: block;
      transform: translate(0, -100%);
      transition: transform .3s;
    }
  }



  &:after{
    position: absolute;
    content: '';
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    z-index: 1;
    background: fade(@dark,20%);
  }

  &:hover{
    &:before{
      transform: translate(0, 0);
    }
  }
}

.leader-item__content{
  position: absolute;
  z-index: 3;
  left: 26px;
  bottom: 30px;
  right: 26px;
  color: #fff;
  overflow: hidden;
}

.leaders-slider__wrp{
  .swiper-pagination{
    @media @nomobile{
      display: none;
    }
  }
}

.h-list__count{
  font-size: 48px;
  font-weight: 700;
  color: @white;
}

.h-list__count--descr{
  font-size: 18px;
  color: @white;
  max-width: 180px;
}

.icon-b{
  padding-right: @base-padding-d;
  border-right: 1px solid @white;
  height: 85px;
  display: flex;
  align-items: center;
}

.content-b{
  margin-left: @base-padding-d;
  height: 85px;
}