.mobile-title__sp,
.slider__item--title__firm{
  font-size: 18px;
  color: @main-red;
  padding: 16px 0;
}

.l25__descr-text{
  font-weight: 300;
  font-size: 25px;
  font-family: @base-font;
}

.box-content__title{
  margin-bottom: 25px;
}

.box-content__title{
  font-size: 18px;
}

.box-content__text{
  margin-bottom: 25px;
}

.tab-title{
  font-weight: 400;
  font-size: 18px;
  padding-bottom: 6px;
  border-bottom: 1px solid @gray-sand;
  margin-bottom: 20px;
}

.tab-title__map{
  @media @mobile-xs{
    display: none;
  }
}

.box-location{
  .mobile-title__sp{
    color: @main-gray;
    padding:15px 0;
    margin: 0 @base-padding-m;
    border-bottom: 1px solid #d7dae0;

    &.b-on{
      border-bottom: none;
      color: @blue;

      .icon{
        transform: rotate(270deg);
        fill: @blue;
      }
    }
  }
}
