

.load-more-wrp{
  padding: @base-padding-m;

  @media @nomobile{
    display: none;
  }

  .load-more-btn{
    width: 100%;
    max-width: 280px;
    margin: 0 auto;
    background: @blue-ocean;
    display: block;
    padding: 15px 0;
    color: @white;
    text-align: center;
    font-size: 15px;
    font-weight: 700;
    text-transform: uppercase;
    transition: background .4s;

    &:hover{
      background: lighten( @blue-ocean,20%);
    }
  }
}


.collapse-button{
  width: 19px;
  height: 19px;
  &.open{
    .icon-bar{

      &:nth-of-type(1){
      transform:translate(0px,8px) rotate(45deg);
      }

      &:nth-of-type(2){
       display: none;
      }

      &:nth-of-type(3){
        transform:translate(0px,2px) rotate(-45deg);
      }
    }
  }
}