
.box-title {
  @base-fz: 20px;
  padding: 9px @base-padding-d;

  @media @mobile-x{
    padding: 9px @base-padding-m;
  }

  .clearfix;
  margin-bottom: 2px;

  .title {
    font-size: @base-fz;
    line-height: @base-fz * 1.2;
    color: @white;
    float: left;

    @media @mobile{
      font-size: 16px;
    }
  }

  .view-all {
    float: right;
    line-height: @base-fz * 1.2;
    text-decoration: underline;
    color: @white;

    &:hover {
      text-decoration: none;
    }
  }
}

.insights-box {
  margin-right: 2px;

  &.box {
    @media @mobile{
      margin-bottom: 2px;
    }
  }

  @media @mobile-xs{
    margin: 0 10px 0 0;
  }
  .box-title {
    background: @main-red;
  }

  .box-content {
    @media @nomobile{
      height: 461px;
    }
    .box-border(@main-red, 2px);

  }
}

.firm-slider__item-col {
  @media @mobile-xs{
    height: 340px;
    overflow: hidden;
  }

  @media @mobile-xx{
    height: 240px;
  }

  &.firm-slider__item-text {
    @media @mobile-xs{
      height: auto;
      padding-bottom: 20px;
    }
  }
}

.news-box {

  .box-title {
    background: @blue-ocean;

  }
}

.firms-box {
  margin-right: 2px;
  .box-border(@main-red, 2px);

  .firm-slider__item-text {
    background: rgba(241, 241, 241, .4);
  }

  @media @mobile{
    margin-bottom: 0;
  }

  .descr-wrp {
    .clearfix;

    .descr {
      float: left;
    }

    .img-wrp {
      float: right;
    }

  }

  .box-title {
    background: @main-red;
  }

  .box-content {
    height: 332px;
    position: relative;
    overflow: hidden;

    @media @mobile-xs{
      height: auto;
      margin-bottom: 0;
    }

    @media @mobile{
      padding: 0 @base-padding-m;;
    }
  }
}

.box-content__attorney {
  .tab-content {
    @media @mobile-xs{
      display: none;
    }
  }

  @media @mobile-xs{
    border-bottom: 1px solid #bdbdbe;
  }
}

.spotlight-title {
  font-size: 18px;
  border-bottom: 1px solid @gray-sand;
  padding-bottom: 8px;
}

.magazine-box {

  .box-border(@main-blue, 2px);

  .box-title {
    background: @main-blue;
  }

  .firm-slider__item-col {
    height: 332px;

    @media @mobile{
      height: auto;
      padding: 0;
    }

    .slider__item--title {
      @media @mobile-xs{
        display: none;
      }
    }
  }

  .box-content {
    display: flex;
    height: 664px;
    overflow: hidden;

    @media @mobile-xs{
      display: block;
      height: 100%;
      overflow: visible;
    }
    .content-left,
    .content-right {
      width: 50%;
      flex-direction: row;

      @media @mobile-xs{
        width: 100%;
        max-height: 400px;
        overflow: hidden;
      }
      img {
        @media @mobile{
          width: 100%;
          height: auto;
        }
      }

    }

    .content-right {
      @media @mobile-xs{
        max-height: 100%;
        height: auto;
      }
    }
    .content-left {
      position: relative;

      .content {
        position: absolute;
        height: 332px;
        left: 50px;
        right: 40px;
        z-index: 2;
        bottom: 0;
        color: @white;
        width: 200px;

        @media @mobile-xs{
          height: auto;
          left: 20px;
          bottom: 40px;
        }

        .sponsored {
          margin-bottom: 6px;
        }

        .name {
          font-size: 16px;
          margin-bottom: 8px;
        }

        .text {
          line-height: 1.5;
        }
      }
    }
  }
}

.box {
  &.history-box {
    @media @desktop-xl{
      width: calc(~'66.66% - 1px');
    }
  }
}

.history-box {
  .box-title {
    background: @main-red;
  }

  .col-right {
    @media @nomobile-xs{
      width: 45%;
    }

    @media @mobile-xs{
      float: none;
    }

  }

  .col-left {
    @media @nomobile-xs{
      margin-right: 5%;
      width: 50%;
      padding: 40px 0 0 @base-padding-d;
    }

    @media @mobile-xs{
      float: none;
      padding: @base-padding-m * 2 @base-padding-m;
    }
  }

  .box-content {
    .clearfix;
    background: @bg-sand;
  }
}

.box {
  &.box-hightlights {
    @media @desktop-xl{
      width: calc(~'33.5% - 1px');
      display: flex;
      flex-direction: column;
    }
  }
}

.box-hightlights {
  .box-title {
    background: @main-blue;
  }

  .box-content {
    padding: 45px;
    background: url('../img/hightlights-bg.jpg') no-repeat;
    background-size: cover;

    @media @desktop-xl{
      flex: 1 0 auto;
    }

  }

}

.link-wrp {
  a {
    color: @white;
    float: right;
    margin-top: 25px;

    &:hover {
      text-decoration: underline;
    }
  }
}

.advisory-box {
  margin-right: 2px;

  &.box {
    @media all and (max-width: 768px) {
      margin-bottom: 2px;
    }
  }

  .box-border(@dark-blue, 2px);

  .box-title {
    background: @dark-blue;
  }

  .advisory-box__title {
    margin-bottom: 28px;
  }

  .box-content {
    padding: 45px 22px 0 22px;

    @media all and (max-width: 768px) {
      padding: 45px 65px 0 65px;
    }
  }
}

.client-box {
  .box-border(@blue, 2px);

  .box-title {
    background: @blue;
  }

  .box-content {
    padding: 45px 22px 85px 22px;

    @media @nodesktop{
      padding: 40px 20px 0 20px;
    }

    background: rgba(24, 141, 194, .1);

    .img-holder {

      @media @desktop{
        float: left;
        width: 60%;
      }

      @media @mobile-xs{
        display: none;
      }
    }

    .features-col {
      float: left;
      width: 40%;

      @media @mobile{
        float: none;
        width: 100%;
        padding-bottom: 40px;
      }
    }
  }
}

.inquiries-box {
  .box-title {
    background: @main-gray;
  }

  .box-content {
    padding: 29px 0 29px @base-padding-d;
    background: #fbfbfb;

    @media @mobile-x{
      padding: @base-padding-m * 2  @base-padding-m @base-padding-m @base-padding-m;
    }
  }
}

.box {
  &.box-location {
    @media @nomobile-x{
      display: flex;
      flex: 1 0 auto;
      flex-direction: column;
    }

    @media @mobile-xs{
      background: #ebf2fb;
    }

    .box-title {
      background: @blue-ocean;
    }

    .box-content {
      @media @nomobile-xs{
        display: flex;
        flex-grow: 1;
        flex-shrink: 0;
        flex-basis: auto;
      }

    }
  }
}

.box-leaders {
  .box-title {
    background: @blue;
  }
}

.box-releases {
  .box-title {
    background: @blue-ocean;
  }
}

.box-partners {
  .box-border(@main-gray, 2px);

  .swiper-container {
    position: static;
  }

  .box-title {
    background: @main-gray;
  }

  .box-content {
    padding: 0 100px;
    position: relative;
    background: fade(@brow-sand, 30%);

    @media @mobile{
      padding: 0 70px;
    }
  }

  .swiper-button-next,
  .swiper-button-prev {
    @media @desktop{
      display: none;
    }

  }

  &:hover {
    .swiper-button-next,
    .swiper-button-prev {
      display: block;
    }
  }
}

.box-content__firm {
  @media @mobile{
    margin-bottom: 35px;
  }

  .tab-content {
    @media @mobile-xs{
      display: none;
    }
  }

  .firm-slider__item-col {
    img {
      height: auto;
      width: auto;
    }

  }

}

.firms-box {
  .box-content__firm {
    .swiper-pagination {
      @media @nomobile-xs{
        left: auto;
        right: 25%;
        transform: translate(50%, 0);
      }
    }
  }
}

.box-content__attorney {
  .firm-slider__item-col {
    .flex-container {
      @media @mobile{
        display: block;
      }
    }
  }
}

.magazine-box {
  .firm-slider__item-text {
    @media @mobile-xs{
      height: auto;
    }

    @media @mobile{
      .descr {
        padding-top: 10px;
      }
    }
  }

  .firm-slider__item-col {
    &.issue,
    &.metro {
      @media @mobile-xs{
        height: auto;
      }
    }
  }
}

.box-content__attorney {
  .swiper-pagination {
    @media @mobile-xs{
      top: 50% !important;
    }

    @media @mobile-x{
      top: 43% !important;
    }
  }
}

.box {
  float: left;
  width: calc(~'50% - 1px');

  @media @nodesktop-xl{
    float: none;
    width: 600px;
    margin: 0 auto 30px auto;
  }

  @media @mobile{
    width: 100%;
  }

  .slider__item--title {
    font-size: 18px;
    border-bottom: 1px solid @gray-sand;
    padding-bottom: 8px;
    color: @main-red;

    @media @nomobile{
      margin-bottom: @base-padding-d;
    }

  }
}

.firms-box {
  @media @mobile{
    margin-bottom: 2px;
  }
}