body{
  &.about-us{
    .heading-wall{
      background: url('../img/about-bg.jpg') no-repeat;
      background-size: cover;
    }
  }

  &.contact-us{
    .heading-wall{
      background: url('../img/contact-bg.jpg') no-repeat;
      background-size: cover;
    }
  }
}

.heading-wall{
  height: 250px;
  display: flex;
  align-items: center;
  align-content: center;
  margin-bottom: @base-b-margin;

  @media @mobile-xx{
    height: 160px;
  }
}


