.footer-top {
  text-align: left;
}

.footer-col__2 {
  display: inline-block;
  width: 16.4%;
  vertical-align: top;
  padding-right: 10px;

  &:last-child {
    @media @nomobile-xs{
      padding-right: 0;
    }

  }


  @media @nodesktop-xl {
    width: 33%;
    margin-bottom: 20px;
  }

  @media @mobile-xs{
    width: 49.5%;
  }

  @media @mobile-x{
    width: 100%;
    display: block;
    margin-bottom: 0;
    border-bottom: 1px solid #c2c2c3;
    padding: 0 20px;

    &:first-child{
      border-top: 1px solid #c2c2c3;
    }

    .footer-menu__box-title {
      margin-bottom: 0;
    }
  }
}

.footer-top {
  margin-bottom: 100px;
  padding: 0 @base-padding-d;
  .clearfix;

  @media @mobile{
    margin-bottom: 25px;
    padding: 0 20px;
  }

  @media @mobile-x{
    padding: 0;
  }

  .footer-menu__item {
    margin-bottom: 10px;

  }

  .footer-menu__link {
    font-weight: 700;

  }
}

.footer-menu__list {

  @media @mobile-x {
    margin-top: 15px;
  }

  @media @mobile-x{
    display: none;
  }
}

.footer-menu__box {

  @media @mobile-x{
    margin-bottom: 0;

    padding: @base-padding-m 0;

  }
  .footer-menu__box-title {
    text-transform: uppercase;
    color: @blue;
    font-weight: 700;
    display: block;
    font-size: 15px;
    margin-bottom: 10px;
    .clearfix;

    .arrow {
      width: 20px;
      height: 20px;
      display: none;

      @media @mobile-x{
        display: block;
      }

      &:after {
        @media @mobile-x{
          float: right;
          border: 6px solid transparent;
          border-left: 10px solid @blue;
          content: '';
        }
      }

    }

    @media @mobile-x{
      margin-bottom: 0;
    }
  }

  &.open {
    .arrow {

      &:after {
        @media @mobile-x{
          border-left: none;
          border-right: 10px solid @blue;
        }
      }

    }
  }
}

.footer-links {
  text-align: center;
  position: relative;

  @media @nodesktop{
    margin-bottom: 35px;
  }

  @media @mobile-x{
    text-align: center;

    border-bottom: 1px solid #c2c2c3;
  }


  @media @desktop{
    float: right;
    margin-bottom: 20px;
  }

}

.copyrights {
  font-size: 15px;

  @media @desktop{
    float: left;
  }

  @media @nodesktop{
    text-align: center;
  }

  @media @mobile{

  }
}


.footer-links__item {
  display: inline-block;
  position: relative;
  padding: 0 8px;

  @media @mobile-x{
    display: block;
    margin-bottom: 24px;
  }

  &:hover{
    a{
      color: @main-red;
    }
  }

  &:last-child {
    padding-right: 0;

    &:after {
      content: none;
    }
  }

  &:after {
    content: '';
    position: absolute;
    height: 12px;
    width: 1px;
    background: #939495;
    top: 3px;
    right: 0;

    @media @mobile-x{
      content: none;
    }
  }
}

.footer-bottom {

  margin-bottom: @base-b-margin;

  @media @desktop{
  padding: 0 @base-padding-d;
}

  .clearfix;

  @media @mobile-x{
    margin-bottom: 0;
   padding-bottom: 35px;
    overflow: hidden;
  }

}

.footer-mid {
  margin-bottom: 10px;

  @media @nodesktop{
    margin-bottom: 40px;
  }
}

.footer-menu__item {

  &:hover {
    .footer-menu__link {
      color: @blue;
      &:after {

        left: 0;
        right: 0;
        width: 100%;

      }
    }
  }
}