.main-wall {
  background: url('../img/panel-bg.jpg');
  background-size: cover;
  height: 500px;
  padding: 60px 0;
  color: @white;
  margin-bottom: @base-b-margin;

  @media @mobile{
    height: 315px;
    padding: 35px 0 40px 0;
    margin-bottom: 40px;
  }

  .main-wall__text {
    max-width: @text-width;
    margin: 35px auto 25px auto;
    font-size: 22px;
    text-align: center;

    @media @mobile{
      font-size: 18px;
    }
  }
}


.main-wall__title {
  font-size: 37px;
  text-align: center;
  font-weight: 300;
  margin: 0 auto;
  max-width: @text-width;
  position: relative;
  color: @white;

  @media @mobile {
    font-size: 24px;
  }

  &:after {
    content: '';
    display: inline-block;
    background: @white;
    width: 190px;
    height: 1px;
    bottom: -18px;
    position: absolute;
    left: 50%;
    transform: translate(-50%, 0);
  }
}




.search-lawyer__wrapper {
  max-width: 1115px;
  margin: 0 auto;
  background: rgba(0, 0, 0, .5);
  padding: @base-padding-d 0;

  @media @nodesktop{
    padding: @base-padding-d;
  }

  @media @mobile {
    display: none;
  }

}



.search-lawyer__container {
  max-width: 830px;
  margin: 0 auto;

  h2 {
    overflow: hidden;
    white-space: nowrap;
    text-align: center;
    font-size: 26px;
    margin-bottom: 20px;

    &:before,
    &:after {
      content: "";
      width: 50%;
      display: inline-block;
      border-top: 1px solid;
      vertical-align: middle;
    }

    &:before {
      margin-left: -50%;
      margin-right: 10px;
    }

    &:after {
      margin-right: -50%;
      margin-left: 10px;
    }
  }
}

.news-items-holder {
  .clearfix;

  .box-border(@blue-ocean, 2px);
  .clearfix;
  .news-item,
  .event-item {

    @media @nomobile{
      height: @news-item-h;
      float: left;
      overflow: hidden;
      cursor: pointer;
      position: relative;
      width: 50%;
    }

    img {
      .resp-img;
    }

    .news-item__content {
      position: absolute;
      z-index: 3;
      left: @base-padding-d;
      bottom: 30px;
      right: @base-padding-d;
      color: @white;

      .news-time {
        font-style: italic;
        font-weight: 300;
        font-size: 16px;
      }

      .title {
        font-size: 18px;
      }
    }
  }

  .event-item__link {
    display: block;
    position: relative;
    z-index: 4;
    height: 100%;

    @media @nomobile {
      &:before {
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
        right: 0;
        position: absolute;
        content: '';
        bottom: 0;
        z-index: 2;
        background: rgba(54, 105, 179, .9);
        display: block;
        transform: translate(0, -100%);
        transition: transform .3s;
      }
    }

    &:hover {
      &:before {
        transform: translate(0, 0);
      }
    }
  }
}

.descr-inform {
  .clearfix;
  @padding: 50px;
  background: @light-sand;
  padding: @padding;

  @media @mobile{
    padding: @base-padding-m * 2 @base-padding-m;
  }

  .descr-inform__col-left {
    @media @nomobile{
      width: 55%;
      margin-right: 5%;
      margin-bottom: 35px;
      float: left;
    }

    @media @mobile {
      margin-bottom: 35px;
    }
  }

  .descr-inform__col-right {
    .clearfix;

    @media @nomobile{
      width: 40%;
      float: right;
    }

    img {
      width: 100%;
      height: auto;
    }
  }

  .descr-inform__title {
    font-size: 33px;
    font-weight: 300;
    padding-bottom: 16px;
    border-bottom: 1px solid @gray-sand;
    margin-bottom: 26px;

    @media @nodesktop{
      font-size: 25px;
    }

    @media @mobile-xs{
      font-size: 20px;

    }
  }

  .descr-inform__text {
    font-size: 25px;
    font-weight: 300;

    @media @nodesktop{
      font-size: 20px;
    }

    @media @mobile-xs{
      font-size: 15px;
    }

    a {
      color: @blue;
      border-bottom: 1px solid @blue;
      font-weight: 300;

      &:hover {
        border-color: transparent;
      }
    }
  }
}

.feature-item {

  border-bottom: 1px solid #d4dade;
  padding: 10px 0;

  .feature-title {
    font-size: 18px;
    transition: color .4s;
    cursor: pointer;
    .clearfix;

    &:hover {
      color: @blue;
    }

    &:after {

    }
  }

  &.open {
    .feature-title {
      color: @blue;
    }
  }
}

.advisory-box__descr {
  margin-bottom: 40px;

  @media @nodesktop{
    margin-bottom: 25px;
  }
}

.advisory-box__title {
  font-size: 18px;
}

.features-holder {
  .clearfix;

  .features-col {

    @media @nomobile-xs{
      padding-right: @base-padding-d;
    }

  }
}

.js--tab-title-m {
  @media @nomobile-xs{
    display: none !important;
  }
}

.slider__item--title-d {
  @media @mobile-xs{
    display: none !important;
  }
}

.firm-slider__item-col {
  &.show-content {
    .slider__item--title {

      svg {
        transform: rotate(270deg) !important;

      }
    }
  }

  &.issue {
    @media @mobile{
      padding: @base-padding-m;
    }

    background: rgba(34, 60, 98, 0.1);

    .slider__item--title {
      color: @main-gray;
      svg {
        transform: rotate(90deg);
        fill: @main-gray;
      }
    }
  }

  &.metro {
    background: @main-blue;

    @media @mobile{
      padding: @base-padding-m;
    }

    .slider__item--title {
      color: @white;

      svg {
        transform: rotate(90deg);
        fill: @white;
      }
    }

    .descr {
      .name,
      .text {
        color: @white;
      }
    }
  }
}

.slider-title-n {
  @media @mobile-xs{
    display: none !important;
  }
}

.feature-content {
  p {
    line-height: 1.5;

    &:first-child {
      margin-top: 20px;
    }

    .clearfix;

    a {
      color: @blue;
      text-decoration: underline;
      float: right;
      font-weight: 700;
      font-style: italic;

    }
  }
}

.firms-box {
  .box-content__firm {
    @media @mobile-x{
      margin-bottom: 35px;
      border-bottom: 1px solid #bdbdbe;
    }
  }
}

.mobile-title__sp {
  .clearfix;

  svg {
    width: 18px;
    height: 15px;
    fill: #6e6f71;
    transform: rotate(90deg);
    float: right;
  }

  display: none;

  @media @mobile-xs{
    display: block;
  }
}

.box-content {
  &.show-content {
    .mobile-title__sp,
    .slider__item--title
    .slider__item--title__firm {
      .clearfix;

      svg {

        fill: @main-red;
        transform: rotate(270deg);
        float: right;
      }
    }
  }
}

.box-content__attorney {
  &.open {
    .mobile-title__sp {
      svg {
        transform: rotate(270deg);
      }
    }
  }
}

.magazine-box {
  .firm-slider__item-text {

    @media @mobile-x{

    }

    .slider__item--title {
      @media @mobile-xs{
        display: block;
      }
    }
  }
}

.firm-slider__item-text {
  .slider__item--title {
    @media @mobile-xs{
      display: none;
    }
  }
}

.firm-slider__item-col {
  .slider__item--title {
    .icon {
      display: none;

      @media @mobile-xs{
        display: block;
      }
    }
  }
}

.read-more-wrp {
  .clearfix;
  margin-top: 20px;
  a {
    float: right;
    font-weight: 700;
    font-style: italic;

    &:hover {
      text-decoration: underline;
    }
  }
}

.firm-slider__item-col {
  &.metro {
    .read-more-wrp {
      .clearfix;

      a {
        color: @white;
      }
    }
  }
}

.news-items-holder {
  &.show {
    .load-more-btn {
      display: none;
    }
  }
}




