.testimonials-wrp{
  background: @light-sand;
  padding: 85px 0 45px 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.testimonials-content{
  max-width:@testimonials-w;

  .testimonials-bq{
    font-size: 25px;
    font-weight: 300;
    text-align: center;
    margin-bottom: 25px;
    position: relative;

    &:before{

     content: '';
      background: url('data:image/png;base64,');
      display: block;
      width: 39px;
      height: 30px;
      position: absolute;
      top: -25px;
      left: -60px;

      @media @nodesktop{
        left: 50%;
        transform: translate(-50%,0);
        position: relative;
        top: -18px;
      }
    }

    &:after{
      background: url('data:image/png;base64,');
      display: block;
      width: 39px;
      height: 30px;
      content: '';
      position: absolute;
      bottom: -12px;
      right: -60px;

      @media @nodesktop{
        right: -50%;
        position: relative;
        transform: translate(-50%,0);
      }
    }

    @media @nodesktop{
      font-size: 18px;
    }

  }

  .img-holder{
    max-width: 440px;
    display: flex;
    margin: 0 auto;
    justify-content: space-around;
    padding:24px 0 0 0;
    border-top: 1px solid @gray-sand;

    @media @mobile{
      flex-direction: column;
    }

    div{

    }

    img{
      display: block;
      width: auto;
      height: auto;
      margin: 0 auto;
    }
  }

}

.testimonials-bq{

}

.descr-t{
  .name{
    font-style: italic;
    font-weight: 700;
    font-size: 16px;
    color: @blue;

    @media @mobile {
    text-align: center;
    }
  }

  .position{
    font-style: italic;
    font-size: 14px;
    color: @blue;

    @media @mobile {
      text-align: center;
    }
  }
}

.logo-t{
  @media @mobile{
    margin-bottom: 20px;
  }
}
