.map-item__wrp {
  width: calc(~'50% - 1px');
  background: #ebf2fb;
  flex-direction: column;
  margin-right: 1px;

  @media @nomobile-xs{
    display: flex!important;
  }

  @media @mobile-x{
    width: 100%;
  }

  @media @mobile-xs{
    width: 100%;
    display: none;
  }
  
  &.show-content{
    @media @mobile-xs{
      border-bottom: 1px solid #d7dae0;
    }
  }
  
}

#map-re,
#map-ps {
  height: 292px;
  margin-bottom: 22px;

  @media @mobile-x{
    order: 2;
  }
}

.tab-content--map {
  p {
    margin-bottom: 10px;
  }
}

.map-item__content {
  padding: 0 @base-padding-d;
  order: 2;
}

.directions {
  a {
    color: @blue;
    text-decoration: underline;
    font-style: italic;

    &:hover {
      text-decoration: none;
    }
  }
}