* {
  box-sizing: border-box;
}

html {
  min-height: 100%;
  height: 100%;
  font-size: 100%;
  -webkit-text-size-adjust: 100%;
}

body {
  font-size: 14px;
  font-family: @base-font;
  color: @main-gray;
}

.container {
  max-width: @container-w-d;
  margin: 0 auto;
  .clearfix;
}

.col-left {
  float: left;
}

.col-right {
  float: right;
}

a {
  color: @main-gray;
  text-decoration: none;
}

.resp-img() {
  width: 100%;
  height: auto;
  display: block;
}


.flex-section__container{
  display: flex;

  @media @nodesktop-xl{
    display: block;
  }
}
.img-resp{
  .resp-img;
}

.section-container {
  .clearfix;
  max-width: @container-w-d;
  margin: 0 auto @base-b-margin auto;

  @media @mobile{
    margin: 0 auto 40px auto;
  }
}

.flex-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}

img {
  display: block;

  @media @mobile{
    width: 100%;
    height: auto;
  }
}

.flex-section__advisory {
  display: flex;

  @media @nodesktop-xl{
    display: block;
  }
}