.icon-cart {
  width: 19px;
  height: 17px;
  fill: @main-gray;

  @media @nodesktop{
    fill: @main-red;
  }
}

.icon-cross {
  fill: @white;
  width: 20px;
  height: 20px;
}

.social-holder {
  text-align: center;
}

.social-item {
  display: inline-block;
  vertical-align: middle;
  margin-right: 35px;

  @media @mobile-x{
    margin-right: 15px;

    &:last-child {
      margin-right: 0;
    }
  }

  &:last-child {
    margin-right: 0;
  }
}

.social-list {
  text-align: center;
  overflow: hidden;
  white-space: nowrap;
  vertical-align: middle;

  &:before,
  &:after {
    content: "";
    width: 50%;
    display: inline-block;
    border-top: 1px solid #c2c2c2;
    vertical-align: middle;
  }

  &:before {
    margin-left: -50%;
    margin-right: 25px;
  }

  &:after {
    margin-right: -50%;
    margin-left: 25px;
  }

}

.social-item {
  &:hover {
    .social-item__link {
      svg {
       fill: @blue-ocean;
      }
    }

  }
  .social-item__link {
    svg {
      width: 17px;
      height: 17px;
      fill: @main-blue;
      transition: all .4s;

      @media @mobile-x{
        width: 27px;
        height: 27px;
      }
    }
  }


}

.arrow {
  float: right;
  transform: rotate(90deg);

  svg {
    width: 16px;
    height: 16px;
    fill: @main-gray;
  }

}

.feature-item {
  &.open {
    .arrow {
      float: right;
      transform: rotate(270deg);

      svg {
        fill: @blue;
      }
    }
  }
}

.search-icon-h {
  margin-right: 10px;

  @media @mobile{
    display: none;
  }
}

.icon-search {
  width: 24px;
  height: 24px;
  fill: @main-gray;
}

.login-icon__nav{

  svg{
    display: none;
  }

  @media @nodesktop{
   svg{
     display: block;
      fill: none;
     stroke: @main-red;
     width: 17px;
     height: 19px;
   }
  }
}

.magazine-box{
  .slider__item--title{
    .clearfix;

    .icon{
      width: 17px;
      height: 17px;
      float: right;
      fill:@main-gray;
    }
  }
}


.icon-h{
  width: 45px;
  height: 45px;
  fill: @white;
}

.icon-cm{
  width: 15px;
  height: 15px;
  fill: @main-gray;
  margin-right: 10px;
}

.icon-mail-f{
  fill: @blue;
  transform: rotate(240deg);
}