.swiper-slide {
  img {
    width: 100%;
    height: auto;
  }
}

.firm-slider__item {
  .clearfix;

  .firm-slider__item-col {

    @media @nomobile-xs{
      width: 50%;
      height: 332px;
      float: left;
    }

    .content-wrp {

    }

    .award{
      font-size: 18px;
      color: @main-red;
      font-weight: 700;
      margin: 20px 0 10px 0;
    }
  }

  .img-wrp {
    width: 58px;
    height: 64px;
    
    img{
      width: auto;
      height: auto;
    }
  }

}

.descr{
  .text{
    line-height: 1.2;
  }

  .name{
    font-size: 16px;
  }
}

.firm-slider__item-text{
  padding: 42px @base-padding-d 0 @base-padding-d;
}

.partners-slider{
  .brand-item{
    position: relative;
    display: flex;
    height: auto;
    align-items: center;

    a{
      display: block;
      width: 100%;
    }

    @media @desktop{
      filter:grayscale(100%);
      opacity: .4;
    }

    transition: all .8s;

    &:hover{
      filter: none;
      opacity: 1;
    }

    img{
      width: 100%;
      height: auto;
    }

    .color{
      display: none;
      opacity: 0;
      transition: all .4s;
      width: 100%;
      height: auto;
    }
  }
}


.insights-slider{

}

.insights-slider__item{
  @media @nomobile{
    height: 456px;
  }

  overflow: hidden;
  position: relative;
}

.insights-slider__item-content{
  position: absolute;
  z-index: 2;
  left: 75px;
  bottom: 50px;

  @media @mobile{
    left: @base-padding-m;
  }

  .title{
    font-weight: 300;
    font-size: 36px;
    color: @white;

    @media @mobile{
      font-size: 24px;
    }
  }

  .descr{
    font-style: italic;
    font-size: 16px;
    color: @white;
  }
}

.board-slider-wrp{

  @media all and (max-width: 768px){
    position: static;  
  }
  
  .swiper-pagination-bullets,
  .swiper-button-next,
  .swiper-button-prev{
    display: none;

    @media all and (max-width: 768px){
      display: block;
    }
  }
}

.board-slider__content{
  margin-top: 20px;

  .name{
    font-size: 16px;
    margin-bottom: 6px;
  }

  .position,
  .firm{
    margin-bottom: 6px;
  }

  .position{
    font-weight: 700;
  }
}


.partners-slider{
  height: 220px;
  display: flex;
  align-items: center;
}


.swiper-pagination-bullet{
  height: 12px;
  width: 12px;
  opacity: 1;
  background: @white;

  &-active{
    background: @main-red;
  }
}

.box-content__firm{
  .swiper-pagination-bullet{
    background: @main-gray;

    &-active{
      background: @main-red;
    }

  }
}

.swiper-button-next,
.swiper-button-prev,
.swiper-container-rtl .swiper-button-prev{
  background: none;
  width: 25px;
  height: 25px;
  border: 1px solid #dcdcdc;
  padding: 5px;
  margin-top: -12px;

  svg{
    width: 15px;
    height: 15px;
    fill: @main-gray;
  }
}

.swiper-button-prev{
  transform: rotate(180deg);
}

.swiper-button-prev{
  left: 20px;
}
.swiper-button-next{
  right: 20px;
}


.advisory-box{

  @media all and (max-width: 768px){
    padding-bottom: 80px;
  }

  .box-content{
    @media all and (max-width: 768px){
      position: relative;

      .swiper-pagination-bullet{
        background: @main-gray;

        &-active{
          background: @main-red;
        }
      }

      .swiper-pagination{
        bottom: -30px;
      }
    }
  }
}

.box-content__attorney{
  .swiper-pagination{
    @media @mobile-x{
      top: 80%;
    }
  }
}

.firms-box{
  .swiper-pagination{
    @media @nomobile{
      width: auto;
      left: 25%;
      transform: translate(-50%,0);
    }


  }
}



  .swiper-wrapper{
    &.leaders-slider{
      @media @nomobile{
        display: block;
      }
    }
  }

